@import url(https://fonts.googleapis.com/css2?family=Quicksand:wght@400;700&display=swap);
/* Universal Styles */

html {
  font-size: 18px;
}

@media only screen and (max-width: 560px) {
  html {
    font-size: 15px;
  }
}

input:focus {
  outline: none;
}

/* Reusable Component - Image Container */

.image-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  overflow: hidden;
  background: #000;
}

.image-container img {
  width: 100%;
}

/* App Styles */

.App {
  font-family: 'Work Sans', sans-serif;
}

h1 {
  padding: .66rem 0;
  text-align: center;
  background-color: #537dcc;
  font-family: 'Quicksand', sans-serif;
  font-size: 1.8rem;
  color: #fff;
}
.BuildingList {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
  margin: 4.4rem 10%;
  font-family: 'Quicksand', sans-serif;
}
.Building {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  margin: 0 .5rem 2.3rem .5rem;
  font-family: 'Quicksand', sans-serif;
}

.Building .image-container {
  width: 21rem;
  height: 14rem;
  margin-bottom: 1rem;
}

.Building h2 {
  margin-bottom: .5rem;
  font-size: 1.2rem;
  font-weight: 600;
}

.Building-information {
  display: flex;
  justify-content: space-between;
}

.Building-information p {
  font-size: .88rem;
  font-weight: 300;
  line-height: 1rem;
}

.Building-address {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.Building-reviews {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  text-align: right;
}

.Building-reviews h3 {
  color: #CCA353;
  font-weight: 600;
}

.Building-reviews .rating {
  font-size: .88rem;
  line-height: 1rem;
}
.SearchBar {
  display: flex;
  flex-direction: column;
  justify-content: center;
  background-image: url(/bygningspas/static/media/concrete-texture-bg.95396712.jpg);
  background-size: cover;
  background-repeat: no-repeat;
  height: 15rem;
}

.SearchBar-sort-options ul {
  display: flex;
  justify-content: center;
  margin-bottom: 2.22rem;
  color: #ffffff;
}

.SearchBar-sort-options li {
  cursor: pointer;
  width: 4.33rem;
  border-bottom: 1px solid #fff;
  padding: 0 2.58rem .33rem 2.58rem;
  line-height: 1.13;
  text-align: center;
  font-weight: 600;
  font-size: .83rem;
  transition: color .25s;
}

.SearchBar-sort-options li:hover {
  color: #d4cfcf;
}

.SearchBar-sort-options li.active,
.SearchBar-sort-options li.active:hover {
  border-bottom: 1px solid #537dcc;
  color: #537dcc;
}

.SearchBar-fields {
  display: flex;
  justify-content: center;
  margin-bottom: 2.88rem;
}

.SearchBar-fields input {
  width: 12rem;
  padding: .66rem 1rem;
  margin-right: 2.22rem;
  border: 1px solid #fff;
  border-radius: 4px;
  font-size: .77rem;
  font-weight: 500;
}

.SearchBar-fields input:last-child {
  margin-right: 0;
}

.SearchBar-submit {
  text-align: center;
}

.SearchBar-submit a {
  border-radius: 4px;
  padding: .72rem 1.7rem;
  background-color: #537dcc;
  color: #ffffff;
  font-weight: 600;
  transition: background-color .5s;
}

.SearchBar-submit a:hover {
  cursor: pointer;
  background-color: #537dcc;
}

@media only screen and (max-width: 560px) {
  .SearchBar {
    background-image: url(/bygningspas/static/media/background_search_mobile.a6f8ee50.jpg);
  }

  .SearchBar-sort-options ul {
    margin-left: 2rem;
    margin-right: 2rem;
  }

  .SearchBar-fields {
    flex-direction: column;
    align-items: center;
  }

  .SearchBar-fields input {
    margin-right: 0;
    margin-bottom: .86rem;
  }
}
