.Building {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  margin: 0 .5rem 2.3rem .5rem;
  font-family: 'Quicksand', sans-serif;
}

.Building .image-container {
  width: 21rem;
  height: 14rem;
  margin-bottom: 1rem;
}

.Building h2 {
  margin-bottom: .5rem;
  font-size: 1.2rem;
  font-weight: 600;
}

.Building-information {
  display: flex;
  justify-content: space-between;
}

.Building-information p {
  font-size: .88rem;
  font-weight: 300;
  line-height: 1rem;
}

.Building-address {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.Building-reviews {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  text-align: right;
}

.Building-reviews h3 {
  color: #CCA353;
  font-weight: 600;
}

.Building-reviews .rating {
  font-size: .88rem;
  line-height: 1rem;
}